	module.exports = {"pathContext":{"docHtml":"<h1>FAQ</h1>\n<p>This is being added to as common issues occur on the <a href=\"http://github.com/remy/nodemon/issues\">issues</a>, and where appropriate the answers will be added here.</p>\n<p>This is a working document, and if it makes sense, I'll take pull requests to help make it better.</p>\n<h2>nodemon doesn't work with my REPL</h2>\n<p>Create an nodemon.json file with the setting:</p>\n<pre><code class=\"language-js\">{\n  \"restartable\": false\n}\n</code></pre>\n<p>This will leave the STDIN to your application rather than listening for the <code>rs</code> command to restart.</p>\n<h1>My script arguments are being taken by nodemon</h1>\n<p>Use the <code>--</code> switch to tell nodemon to ignore all arguments after this point. So to pass <code>-L</code> to your script instead of nodemon, use:</p>\n<pre><code>$ nodemon app.js -- -L -opt2 -opt3\n</code></pre>\n<p>nodemon will ignore all script arguments after <code>--</code> and pass them to your script.</p>\n<h1>Can't install nodemon: permission issue</h1>\n<p>You may need to install nodemon using <code>sudo</code> (which isn't recommended, but I understand it's unavoidable in some environemnts). If the install fails with this appearing in the npm error log, then you need the following workaround.</p>\n<pre><code>gyp WARN EACCES user \"root\" does not have permission to access the dev dir \"&#x3C;some-local-dir>\"\n</code></pre>\n<p>Try to re-install adding <code>--unsafe-perm</code> to the arguments:</p>\n<pre><code>sudo npm install -g nodemon --unsafe-perm\n</code></pre>\n<p>Ref <a href=\"https://github.com/remy/nodemon/issues/713\">#713</a></p>\n<h1>Help! My changes aren't being detected!</h1>\n<p>nodemon (from 1.4.2 onwards) uses <a href=\"https://www.npmjs.com/package/chokidar\">Chokidar</a> as its underlying watch system.</p>\n<p>If you find your files aren't being monitored, either nodemon isn't restarting, or it reports that zero files are being watched, then you may need the polling mode.</p>\n<p>To enable polling use the the legacy flag either via the terminal:</p>\n<pre><code class=\"language-shell\">$ nodemon --legacy-watch\n$ nodemon -L # short alias\n</code></pre>\n<p>Or via the <code>nodemon.json</code>:</p>\n<pre><code class=\"language-json\">{\n  \"legacyWatch\": true\n}\n</code></pre>\n<h2>nodemon tries to run two scripts</h2>\n<p>If you see nodemon trying to run two scripts, like:</p>\n<pre><code>9 Dec 23:52:58 - [nodemon] starting `node ./app.js fixtures/sigint.js`\n</code></pre>\n<p>This is because the main script argument (<code>fixtures/sigint.js</code> in this case) wasn't found, and a <code>package.json</code>'s main file <em>was</em> found. ie. to solve, double check the path to your script is correct.</p>\n<h2>What has precedence, ignore or watch?</h2>\n<p>Everything under the ignore rule has the final word. So if you ignore the <code>node_modules</code> directory, but watch <code>node_modules/*.js</code>, then all changed files will be ignored, because any changed .js file in the <code>node_modules</code> are ignored.</p>\n<p>However, there are defaults in the ignore rules that your rules will be merged with, and not override. To override the ignore rules see <a href=\"#overriding-the-underlying-default-ignore-rules\">overriding the underlying default ignore rules</a>.</p>\n<h2>Overriding the underlying default ignore rules</h2>\n<p>The way the ignore rules work is that your rules are merged with the <code>ignoreRoot</code> rules, which contain <code>['.git', 'node_modules', ...]</code>. So if you ignore <code>public</code>, the ignore rule results in <code>['.git', 'node_modules', ..., 'public']</code>.</p>\n<p>Say you did want to watch the <code>node_modules</code> directory. You have to override the <code>ignoreRoot</code>. If you wanted this on a per project basis, add the config to you local <code>nodemon.json</code>. If you want it for all projects, add it to <code>$HOME/nodemon.json</code>:</p>\n<pre><code class=\"language-json\">{\n  \"ignoreRoot\": [\".git\"]\n}\n</code></pre>\n<p>Now when ignoring <code>public</code>, the ignore rule results in <code>['.git', 'public']</code>, and nodemon will restart on <code>node_modules</code> changes.</p>\n<h2>nodemon doesn't work with fedora</h2>\n<p>Fedora is looking for <code>nodejs</code> rather than <code>node</code> which is the binary that nodemon kicks off.</p>\n<p>The solution is a simple workaround, Linux 101:</p>\n<pre><code class=\"language-bash\">sudo ln -s /usr/bin/nodejs /usr/local/bin/node\n</code></pre>\n<p>Fedora and Ubuntu pakage node as nodejs, because node.dpkg is</p>\n<blockquote>\n<p>Description-en: Amateur Packet Radio Node program\nThe node program accepts TCP/IP and packet radio network connections and\npresents users with an interface that allows them to make gateway connections\nto remote hosts using a variety of amateur radio protocols.\nThey make the binary is nodejs, rather than node. So long as you're not using that Packet Radio Node Program mentioned above the workaround will work.</p>\n</blockquote>\n<p>Thank you <a href=\"https://github.com/remy/nodemon/issues/68#issuecomment-13672509\">@EvanCarroll</a></p>\n<h2>Using nodemon with forever</h2>\n<p>If you're using nodemon with <a href=\"https://github.com/foreverjs/forever\">forever</a> (perhaps in a production environment), you can combine the two together. This way if the script crashes, forever restarts the script, and if there are file changes, nodemon restarts your script. For more detail, see <a href=\"https://github.com/remy/nodemon/issues/30\">issue 30</a>.</p>\n<p>To achieve this you need to add the following on the call to <code>forever</code>:</p>\n<ul>\n<li>Use forever's <code>-c nodemon</code> option to tell forever to run <code>nodemon</code> instead of <code>node</code>.</li>\n<li>Include the nodemon <code>--exitcrash</code> flag to ensure nodemon exits if the script crashes (or exits unexpectedly).</li>\n<li>Tell forever to use <code>SIGTERM</code> instead of <code>SIGKILL</code> when requesting nodemon to stop. This ensures that nodemon can stop the watched node process cleanly.</li>\n<li>Optionally add the <code>--uid</code> parameter, adding a unique name for your process. In the example, the uid is set to <code>foo</code>.</li>\n</ul>\n<pre><code class=\"language-bash\">forever start --uid foo --killSignal=SIGTERM -c 'nodemon --exitcrash' server.js\n</code></pre>\n<p>To test this, you can kill the server.js process and forever will restart it. If you <code>touch server.js</code> nodemon will restart it.</p>\n<p>To stop the process monitored by forever and nodemon, simply call the following, using the <code>uid</code> we assigned above (<code>foo</code>):</p>\n<pre><code class=\"language-bash\">forever stop foo\n</code></pre>\n<p>This will stop both nodemon and the node process it was monitoring.</p>\n<p>Note that I <em>would not</em> recommend using nodemon in a production environment - but that's because I wouldn't want it restart without my explicit instruction.</p>\n<h2>What does \"verbose\" give me?</h2>\n<p>The <code>--verbose</code> (or <code>-V</code>) puts nodemon in verbose mode which adds some detail to starting and restarting.</p>\n<p>Additional restart information:</p>\n<ul>\n<li>Which nodemon configs are loaded (local and global if found)</li>\n<li>Which ignore rules are being applied</li>\n<li>Which file extensions are being watch</li>\n<li>The process ID of your application (the <code>child pid</code>)</li>\n</ul>\n<p>For example:</p>\n<pre><code class=\"language-text\">14 Apr 15:24:58 - [nodemon] v1.0.17\n14 Apr 15:24:58 - [nodemon] reading config /Users/remy/Sites/jsbin-private/nodemon.json\n14 Apr 15:24:58 - [nodemon] to restart at any time, enter `rs`\n14 Apr 15:24:58 - [nodemon] ignoring: /Users/remy/Sites/jsbin-private/.git/**/* node_modules/**/node_modules\n14 Apr 15:24:58 - [nodemon] watching: /Users/remy/Sites/jsbin/views/**/* /Users/remy/Sites/jsbin/lib/**/* ../json/*.json config.dev.json\n14 Apr 15:24:58 - [nodemon] watching extensions: json,js,html\n14 Apr 15:24:58 - [nodemon] starting `node run.js`\n14 Apr 15:24:58 - [nodemon] child pid: 9292\n</code></pre>\n<p>When nodemon detects a change, the following addition information is shown:</p>\n<ul>\n<li>Which file(s) triggered the check</li>\n<li>Which (if any) rules the file matched to cause a subsequent restart</li>\n<li>How many rules were matched and out of those rules, how many cause a restart</li>\n<li>A list of all the files that <em>successfully</em> caused a restart</li>\n</ul>\n<p>For example, on <code>lib/app.js</code> being changed:</p>\n<pre><code class=\"language-text\">14 Apr 15:25:56 - [nodemon] files triggering change check: ../jsbin/lib/app.js\n14 Apr 15:25:56 - [nodemon] matched rule: **/Users/remy/Sites/jsbin/lib/**/*\n14 Apr 15:25:56 - [nodemon] changes after filters (before/after): 1/1\n14 Apr 15:25:56 - [nodemon] restarting due to changes...\n14 Apr 15:25:56 - [nodemon] ../jsbin/lib/app.js\n\n14 Apr 15:25:56 - [nodemon] starting `node run.js`\n14 Apr 15:25:56 - [nodemon] child pid: 9556\n</code></pre>\n<h2>My .nodemonignore is being ignored</h2>\n<p>The new <code>nodemon.json</code> superceeds the <code>.nodemonignore</code> file, so if you have both, the <code>.nodemonignore</code> is not used at all.</p>\n<p>Note that if you have a <code>nodemon.json</code> in your <code>$HOME</code> path, then this will also supersede the old ignore file.</p>\n<h2>nodemon does nothing</h2>\n<p>On Ubuntu globally installed node applications have been found to have no output when they're run. This <em>seems</em> to be an issue with node not being correctly installed (possibly linked to the binary having to be called <code>nodejs</code>).</p>\n<p>The solution (that's worked in the past) is to install <a href=\"https://github.com/creationix/nvm\">nvm</a> first and using it to install node, <em>rather</em> than using <code>apt-get</code> (or similar tools) to install node directly.</p>\n<h2>If nodemon is facing the watch errors (Mac &#x26; Linux)</h2>\n<p>Try the following command on terminal:</p>\n<pre><code class=\"language-bash\">echo fs.inotify.max_user_watches=582222 | sudo tee -a /etc/sysctl.conf &#x26;&#x26; sudo sysctl -p\n</code></pre>\n<h2>Error: Cannot find module 'internal/util/types'</h2>\n<p>If you see the error <code>Cannot find module 'internal/util/types'</code>, the error is solved with a clean npm cache and trying to reinstall the dependency you're working with.</p>\n<p>A start is to use the following commands:</p>\n<pre><code>sudo npm cache clean --force\nsudo npm i -g npm\n</code></pre>\n<p>Otherwise see <a href=\"https://github.com/remy/nodemon/issues/1124\">issue #1124</a> for further suggestions.</p>","docPath":"argo-ci/node_modules/nodemon/faq","proj":"argo-ci"}}

